.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

#gridLayout {

  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  row-gap: 3rem;
}

#gridLayout div {
  margin-right: auto;
  margin-left: auto;
}
.card{
  width: 20rem;
  animation: appearScaled 0.3s linear;
  
}

@keyframes appearScaled {
  from{
    opacity: 0;
    /* scale: 0; */
    transform: translateY(100px);
  }
  to{
    opacity: 1;
    scale: 1;
    transform: translateY(0px);
  }
  
}

@media only screen and (max-width: 1000px) {
  #gridLayout {

    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
  }
  .card{
    width: 17rem;
  }

}

#brand_logo {
  height: 30px;
  width: 30px;
  margin: auto 100px;
}

.navbar-brand {
  margin-left: 10px;
}

@media only screen and (max-width: 400px) {
  #gridLayout {

    width: 100%;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    justify-items: center;
  }
  .card{
    width: 20rem;
  }

}
.pageButtons{
  display: flex;
  justify-content: space-between;
}
#loader h5{
  display: flex;
  gap: 20px;
  /* margin-right: 20px; */
  align-items: center;

}
#loader h5 p{
  margin: auto;
  margin-left: 0px;
}
#heading{
  display: flex;
  justify-content: center;
}
